/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget {
    
    &.woocommerce {
        $thumb: 80px;
        $inner-padding: 15px; // distance between image and content
        $widget-list-item-padding: 15px; // same as sidebar vars
    
        clear: both;
    
        li {
            position: relative;
        }
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // title
    
        .product-title {
            @include mkdfH5();
        
            &:hover {
                color: $first-main-color !important;
            }
        }
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // star rating
    
        li:not(.wc-layered-nav-rating) {
            .star-rating {
                margin-bottom: 7px;
                margin-top: 7px;
            
                span {
                    max-width: 100px;
                }
            }
        }
    
        li.wc-layered-nav-rating {
            a {
                text-decoration: none;
            }
        }
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // price
    
        .amount {
            color: inherit;
        
            span { // currency symbol
                color: inherit;
            }
        }
    
        del,
        ins {
            display: block;
        
            // currency symbol
            span {
                color: inherit;
            }
        }
    
        ins {
            text-decoration: none;
        }
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // layered nav
    
        &.widget_layered_nav {
            .chosen {
                a {
                    color: $first-main-color;
                }
            }
        }
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // price filter
    
        &.widget_price_filter {
            form {
                margin-top: 30px;
            }
        
            .price_slider_wrapper {
                .ui-widget-content {
                    background-color: #c8c8c8;
                    height: 1px;
                    position: relative;
                
                    .ui-slider-handle {
                        background-color: $default-text-color;
                        cursor: pointer;
                        height: 7px;
                        margin: 0 0 0 -1px;
                        outline: medium none;
                        position: absolute;
                        top: -5px;
                        width: 7px;
                        z-index: 2;
                    
                        &:last-child {
                            transform: translateX(-50%);
                        }
                    }
                
                    .ui-slider-range {
                        background-color: $default-text-color;
                        display: block;
                        height: 1px;
                        position: absolute;
                        top: 0;
                        z-index: 1;
                    }
                }
            }
        
            .price_slider_amount {
                margin: 17px 0 0;
            
                .button {
                    float: left;
                    border: none;
                    background-color: transparent;
                    padding: 0;
                    position: relative;
                    color: $first-main-color;
                    display: flex;
                
                    &:after {
                        content: url("../img/arrow.svg");
                        vertical-align: top;
                        margin-left: 9px;
                        display: inline-block;
                    }
                
                    &:hover {
                        background-color: transparent;
                        padding: 0;
                        position: relative;
                        color: $first-main-color;
                    }
                }
            
                .price_label {
                    float: right;
                    line-height: 30px; // to match button height
                    font-size: 16px;
                }
            }
        }
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // widgets with images
    
        &.widget_shopping_cart,
        &.widget_products,
        &.widget_recently_viewed_products,
        &.widget_recent_reviews,
        &.widget_top_rated_products {
            ul {
                li {
                    min-height: $thumb;
                    position: relative;
                
                    a {
                        img {
                            left: 0;
                            max-width: $thumb;
                            position: absolute;
                            top: $widget-list-item-padding;
                            width: 100%;
                        }
                    
                        span {
                            box-sizing: border-box;
                            display: block;
                            padding: 0 0 0 #{$thumb + $inner-padding};
                            position: relative;
                        }
                    }
                
                    > *:not(a) {
                        clear: both;
                        float: left;
                        margin: 0 0 0 #{$thumb + $inner-padding};
                    }
                }
            }
        }
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // widgets with images and plain text next to image
    
        &.widget_shopping_cart {
            a {
                display: block;
                padding: 0 0 0 #{$thumb + $inner-padding};
            
                @include mkdfH5();
            }
        }
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // recent reviews
    
        //&.widget_recent_reviews {
        //    .reviewer {
        //    }
        //}
    
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        // shopping cart
    
        &.widget_shopping_cart {
            .remove {
                font-size: 0;
                font-weight: normal;
                line-height: 1;
                position: absolute;
                right: 0;
                top: 5px;
                text-decoration: none;
            
                &:after {
                    content: "\4d";
                    display: block;
                    font-family: 'ElegantIcons';
                    font-size: 15px;
                    line-height: 15px;
                    position: relative;
                }
            }
        
            .variation {
                @include mkdfRelativeHolderLayout();
            
                > * {
                    display: inline-block;
                    margin: 0;
                    vertical-align: middle;
                
                    p {
                        margin: 0;
                    }
                }
            }
        
            .total {
                margin: 17px 0 13px;
            }
        
            .buttons {
                .button {
                    @include mkdfButtonDefaultStyle();
                    @include mkdfButtonSize();
                    @include mkdfButtonSolidColor();
                
                    &:hover {
                        @include mkdfButtonSolidHoverColor();
                    }
                
                    &:first-child {
                        float: left;
                    }
                
                    &:last-child {
                        float: right;
                    }
                }
            }
        }
    }
    
    // price filter - legacy and block common
    $slider-handle-size: 8px;
    $slider-rail-height: 3px;
    $slider-bottom-margin: 13px;
    
    @mixin qodefWooSliderHandle() {
        background: $first-main-color;
        border: none;
        border-radius: 0;
        height: $slider-handle-size;
        transform: rotate(45deg);
        margin: 0;
        outline: none;
        position: relative;
        top: -#{($slider-handle-size - $slider-rail-height) / 2 };
        width: $slider-handle-size;
    }
    
    // price filter - block
    .wc-blocks-filter-wrapper {
        h1, h2, h3, h4, h5, h6 {
            margin: 0 0 27px;
        }
    }
    
    .wc-block-components-price-slider {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        
        .wc-block-components-price-slider__range-input-wrapper {
            box-shadow: none;
            height: $slider-rail-height;
            margin: calc((#{$slider-handle-size} / 2)) 0;
            width: 100%;
        }
        
        .wc-block-components-price-slider__range-input-progress {
            height: $slider-rail-height;
            background: $first-main-color;
        }
        
        // cant comma-separate these type of selectors
        .wc-block-components-price-slider__range-input {
            
            &::-webkit-slider-thumb {
                @include qodefWooSliderHandle();
            }
            
            &::-moz-range-thumb {
                @include qodefWooSliderHandle();
            }
            
            &::-ms-thumb {
                @include qodefWooSliderHandle();
            }
        }
        
        .wc-block-components-price-slider__controls,
        .wc-block-components-price-slider__range-text {
            margin: $slider-bottom-margin 0 0 auto;
            order: 100;
            
            input {
                margin: 0;
            }
            
            span {
                font-size: initial;
                
                &:first-child {
                    
                    &:after {
                        content: '';
                        margin: 0 4px;
                    }
                }
            }
        }
        
        .wc-block-components-price-slider__actions {
            margin: $slider-bottom-margin 0 0 0;
            
            button {
                border: none !important;
                background-color: transparent !important;
                padding: 0 !important;
                color: $first-main-color !important;
    
                &:after {
                    content: url("../img/arrow.svg");
                    vertical-align: top;
                    margin-left: 9px;
                    display: inline-block;
                }
    
                &:hover {
                    background-color: transparent;
                    padding: 0;
                    position: relative;
                    color: $first-main-color;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */
